import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Navigate, Link } from "react-router-dom";
import { Button, Alert, Row, Col } from "react-bootstrap";
import { useTranslation } from "react-i18next";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import classNames from "classnames";
import axios from "axios";
// actions
import { resetAuth, signupUser } from "../../redux/actions";
import { RootState, AppDispatch } from "../../redux/store";
import { VerticalForm, FormInput } from "../../components/";
import AuthLayout from "./AuthLayout";

interface UserData {
  fullname: string;
  email: string;
  password: string;
}

/* bottom links */
const BottomLink = () => {
  const { t } = useTranslation();

  return (
    <Row className="mt-3">
      <Col className="text-center">
        <p className="text-white-50">
          {t("Already have account?")}{" "}
          <Link to={"/auth/login"} className="text-white ms-1">
            <b>{t("Sign In")}</b>
          </Link>
        </p>
      </Col>
    </Row>
  );
};

/* social links */
const SocialLinks = () => {
  const socialLinks = [
    { variant: "primary", icon: "facebook" },
    { variant: "danger", icon: "google" },
    { variant: "info", icon: "twitter" },
    { variant: "secondary", icon: "github" },
  ];

  return (
    <ul className="social-list list-inline mt-3 mb-0">
      {socialLinks.map((item, index) => (
        <li key={index} className="list-inline-item">
          <Link
            to="#"
            className={classNames("social-list-item", "border-" + item.variant, "text-" + item.variant)}
          >
            <i className={classNames("mdi", "mdi-" + item.icon)}></i>
          </Link>
        </li>
      ))}
    </ul>
  );
};

const Register = () => {
  const { t } = useTranslation();
  const dispatch = useDispatch<AppDispatch>();

  const { loading, userSignUp, error } = useSelector((state: RootState) => ({
    loading: state.Auth.loading,
    error: state.Auth.error,
    userSignUp: state.Auth.userSignUp,
  }));

  useEffect(() => {
    dispatch(resetAuth());
  }, [dispatch]);

  const schemaResolver = yupResolver(
    yup.object().shape({
      fullname: yup.string().required(t("Please enter Fullname")),
      email: yup.string().required("Please enter Email").email("Please enter valid Email"),
      password: yup.string().required(t("Please enter Password")),
    })
  );

  const onSubmit = async (formData: UserData) => {
    try {
      const response = await fetch("http://localhost:8080/ecommerce/auth/signup", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          firstName: formData.fullname.split(' ')[0],
          lastName: formData.fullname.split(' ')[1],
          email: formData.email,
          password: formData.password,
        }),
      });
  
      const data = await response.json();
      
      if (response.ok) {
        sessionStorage.setItem('authToken', data.jwt);
        alert('Registration Successful. Kindly proceed with login');
        dispatch(signupUser(formData.fullname, formData.email, formData.password));
      } else {
        alert(data.message || 'Error in registration');
        console.error('Error in registration', error);
      }
    } catch (error) {
      console.error("API Call Error:", error);
      // alert('Error in registration: '   );
    }
  };
  

  return (
    <>
      {userSignUp ? <Navigate to={"/auth/confirm"} /> : null}

      <AuthLayout
        helpText={t("Don't have an account? Create your account, it takes less than a minute")}
        bottomLinks={<BottomLink />}
      >
        {error && (
          <Alert variant="danger" className="my-2">
            {error}
          </Alert>
        )}

        <VerticalForm<UserData>
          onSubmit={onSubmit}
          resolver={schemaResolver}
          defaultValues={{}}
        >
          <FormInput
            label={t("Full Name")}
            type="text"
            name="fullname"
            placeholder={t("Enter your name")}
            containerClass={"mb-3"}
          />
          <FormInput
            label={t("Email address")}
            type="email"
            name="email"
            placeholder={t("Enter your email")}
            containerClass={"mb-3"}
          />
          <FormInput
            label={t("Password")}
            type="password"
            name="password"
            placeholder={t("Enter your password")}
            containerClass={"mb-3"}
          />
          <FormInput
            label={t("I accept Terms and Conditions")}
            type="checkbox"
            name="checkboxsignup"
            containerClass={"mb-3"}
          />

          <div className="text-center d-grid">
            <Button variant="success" type="submit" disabled={loading}>
              {t("Sign Up")}
            </Button>
          </div>
        </VerticalForm>

        <div className="text-center">
          <h5 className="mt-3 text-muted">{t("Sign up using")}</h5>
          <SocialLinks />
        </div>
      </AuthLayout>
    </>
  );
};

export default Register;
